html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h1, h2, .heading-blanco, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

h2, .heading-blanco {
  text-align: center;
  color: #5e311e;
}
h2::after, .heading-blanco::after {
  content: "";
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  display: block;
  background-image: url("../img/cafe.svg");
  background-size: 10rem;
}
h2 span, .heading-blanco span {
  color: #c39c63;
  display: block;
  font-size: 2.5rem;
}

.heading-blanco {
  color: #fff;
}
.heading-blanco span {
  color: #fff;
}
.heading-blanco::after {
  display: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.header {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000000;
  opacity: 0.6;
}
.header .contenido-header {
  padding: 5rem 0;
  position: relative;
}
.header .texto-header {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .texto-header {
    margin-top: 10rem;
  }
}

.header-inicio {
  background-image: url("../img/header_inicio.jpg");
}

.header-nosotros {
  background-image: url("../img/header_nosotros.jpg");
}

.header-proceso {
  background-image: url("../img/header_proceso.jpg");
}

.header-menu {
  background-image: url("../img/header_menu.jpg");
}

.header-galeria {
  background-image: url("../img/header_galeria.jpg");
}

.header-contacto {
  background-image: url("../img/header_proceso.jpg");
}

.navegacion {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion a {
  font-size: 2rem;
  color: #fff;
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}
.navegacion a:last-of-type {
  margin-bottom: 2rem;
}
.navegacion a:hover {
  color: #c39c63;
}
.navegacion a.activo {
  color: #c39c63;
}

.barra {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .logo {
  width: 50%;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .barra .logo {
    width: 20rem;
  }
}

.contenido-principal {
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .contenido-principal {
    margin-top: -15rem;
    position: relative;
    padding: 10rem;
  }
}
.contenido-principal p {
  text-align: center;
  line-height: 4rem;
}

.iconos {
  display: flex;
  flex-direction: column;
  margin-top: 10rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .iconos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.iconos .icono {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.iconos img {
  width: 25%;
}
@media (min-width: 768px) {
  .iconos img {
    width: 40%;
  }
}
.iconos p {
  color: #5e311e;
  font-size: 3rem;
  font-weight: 4;
  font-family: "Bona Nova", serif;
}

.experiencia {
  margin-top: 5rem;
}
.experiencia .galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .experiencia .galeria {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.experiencia .imagen img {
  width: 30rem;
  height: 30rem;
  object-fit: cover;
  border-radius: 20px;
  transition: 0.5s ease-in;
  box-shadow: 0 0 5px #000000;
}
.experiencia .imagen img:hover {
  transform: scale(1.05);
}
@media (min-width: 1024px) {
  .experiencia .imagen:nth-child(1) {
    grid-row: 1/3;
  }
  .experiencia .imagen:nth-child(1) img {
    height: 62rem;
  }
  .experiencia .imagen:nth-child(2) {
    grid-column: 2/4;
  }
  .experiencia .imagen:nth-child(2) img {
    width: 62rem;
    object-position: top;
  }
  .experiencia .imagen:nth-child(4) {
    grid-row: 2/4;
  }
  .experiencia .imagen:nth-child(4) img {
    height: 62rem;
  }
  .experiencia .imagen:nth-child(5) {
    grid-column: 3/5;
  }
  .experiencia .imagen:nth-child(5) img {
    width: 62rem;
    object-position: top;
  }
  .experiencia .imagen:nth-child(6) {
    grid-column: 3/5;
  }
  .experiencia .imagen:nth-child(6) img {
    width: 62rem;
    object-position: top;
  }
  .experiencia .imagen:nth-child(7) {
    grid-row: 3/5;
  }
  .experiencia .imagen:nth-child(7) img {
    height: 62rem;
  }
  .experiencia .imagen:nth-child(8) {
    grid-column: 2/5;
  }
  .experiencia .imagen:nth-child(8) img {
    width: 94rem;
  }
  .experiencia .imagen:nth-child(9) {
    grid-column: 1/3;
  }
  .experiencia .imagen:nth-child(9) img {
    width: 62rem;
  }
  .experiencia .imagen:nth-child(10) {
    grid-column: 3/5;
  }
  .experiencia .imagen:nth-child(10) img {
    width: 62rem;
  }
}

.section-menu {
  position: relative;
  padding: 15rem 2rem 20rem 2rem;
  background-image: url("../img/menu_arriba.svg"), url("../img/menu_abajo.svg"), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 98%, 100%;
  background-size: 200%;
}
@media (min-width: 768px) {
  .section-menu {
    padding: 15rem 0 20rem 0;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .section-menu {
    background-size: 70%, 70%, 100%;
  }
}
.section-menu ul {
  list-style-type: none;
}

@media (min-width: 768px) {
  .grid-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.menu h3 {
  color: #fff;
}
.menu li {
  display: flex;
  justify-content: space-between;
}
.menu p {
  color: #fff;
  font-size: 2.3rem;
  line-height: 1.5;
  margin: 0;
  margin-bottom: 2rem;
}
.menu span {
  display: block;
  color: #c39c63;
}
.menu .precio {
  align-self: center;
  font-size: 4rem;
  font-weight: bold;
}

.section-testimonios {
  position: relative;
  margin-top: -10rem;
  background-color: #fff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .section-testimonios {
    padding: 10rem;
  }
}

.tarjeta {
  background-color: #ebe7de;
  padding: 3rem;
  max-width: 50rem;
  border-radius: 20px;
  margin: 0 auto;
}
.tarjeta p {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  line-height: 2;
  font-size: 1.6rem;
}
.tarjeta .autor {
  text-align: right;
  margin-top: 4rem;
  color: #5e311e;
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .tarjeta {
    padding: 4rem;
  }
}

.footer {
  background-color: #5e311e;
}
.footer .copy {
  text-align: center;
  margin: 0;
  padding: 3rem 0;
  color: #fff;
}

.grid-contacto {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .grid-contacto {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
    padding-bottom: 0;
  }
}

.contacto {
  display: flex;
  flex-direction: column;
  margin-bottom: 4rem;
}
.contacto h3 {
  color: #c39c63;
  font-family: "Bona Nova", serif;
  font-weight: 100;
  margin-bottom: 2rem;
}
.contacto p {
  font-family: "Bona Nova", serif;
  margin: 1rem;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
.contenido-nosotros .imagen {
  margin: 0 4rem 6rem 0;
}
@media (min-width: 768px) {
  .contenido-nosotros .imagen {
    margin: 0 2rem 0 0;
  }
}
.contenido-nosotros .texto p {
  text-align: left;
}
.contenido-nosotros .texto blockquote {
  margin: 0;
  border-left: 0.6rem solid #5e311e;
  font-size: 2rem;
  padding: 1rem;
}
.contenido-nosotros img {
  transform: rotate(-10deg);
  border: 3rem solid #fff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
}

.proceso {
  margin-bottom: 2rem;
}
.proceso:last-of-type {
  margin-bottom: 0;
}
.proceso:nth-child(even) .texto {
  order: -1;
}
@media (min-width: 768px) {
  .proceso {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.proceso .imagen, .proceso .texto {
  flex: 1;
}
.proceso .texto h3 {
  margin: 0;
  text-align: left;
  color: #5e311e;
  margin-top: 2rem;
}
.proceso .texto h3::after {
  content: "";
  display: block;
  width: 20%;
  height: 5px;
  background-color: #c39c63;
  margin-top: 1rem;
}
.proceso .texto p {
  text-align: left;
  margin-bottom: 2rem;
}
.proceso .imagen img {
  border-radius: 20px;
  box-shadow: 5px 5px 15px #000000;
}
@media (min-width: 768px) {
  .proceso img {
    transition: 0.5s ease;
    border: 2rem solid #fff;
  }
  .proceso img:hover {
    transform: scale(1.05) rotate(-5deg);
  }
}

.contenido-menu {
  background-image: url("../img/cafe.jpg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .contenido-menu {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .contenido-menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.contenido-menu .texto p {
  text-align: left;
}

@media (min-width: 1024px) {
  .contenido-contacto {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url("../img/granos_cafe.jpg");
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.campo {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.campo:last-of-type {
  margin-bottom: 0;
}
.campo input:not([type=submit]),
.campo select {
  padding: 1rem;
  width: 100%;
  border: 4px solid #5e311e;
  font-size: 1.5rem;
}
.campo input[type=submit] {
  background-color: #5e311e;
  width: 100%;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 2.2rem;
  font-weight: bold;
  padding: 1rem;
}
.campo label {
  text-transform: uppercase;
  font-size: 2.2rem;
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.bg-white {
  background-color: #fff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}